.header-search {
    position: relative;
}

.search {
    &-list {
        position: absolute;
        top: 47px;
        list-style-type: none;
        left: 0;
        right: 0;
        max-height: 200px;
        overflow-y: scroll;
        background: $white;
        filter: drop-shadow(0 4px 3px rgb(0 0 0 / 0.07)) drop-shadow(0 2px 2px rgb(0 0 0 / 0.06));
        border-radius: 5px;
        z-index: 50;
        @media(max-width: 992px) {
            background: $borderColor;
        }
        li {
            border-radius: 5px;
            transition: all 0.2s ease-in-out;
            a {
                width: 100%;
                height: 100%;
                padding: 0.5rem 1rem;
                display: inline-block;
                transition: all 0.2s ease-in-out;
                &:hover {
                   transition: all 0.2s ease-in-out;
                   color: $white;
                }
            }
            &:hover {
               transition: all 0.2s ease-in-out;
               background: rgb(107 114 128);
               color: $white;
               cursor: pointer;
               a {
                color: $white;
               }
            }
        }
    }
}