/*****************************
Components - Page Pagination
*****************************/
.page-pagination {
    margin-top: 42px;

    li {
        display: inline-block;
        margin-right: 15px;
        &:last-child{margin-right: 0;}
    }

    li span {
        padding: 5px 10px;
        display: block;
        background: #f1f1f1;
        border-radius: 3px;
        font-weight: 500;
        cursor: pointer;
        transition: all 0.2s ease-in-out;
        &:hover {
            transition: all 0.2s ease-in-out;
            background: $themeColor;
            color: $white;
        }
        &.active{
            background: $themeColor;
            color: $white;
        }
    }
}