*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
}

*::-webkit-scrollbar,
html *::-webkit-scrollbar {
  height: 12px;
  width: 4px;
}
*::-webkit-scrollbar-track,
html *::-webkit-scrollbar-track {
  background: $borderColor;
}
*::-webkit-scrollbar-thumb,
html *::-webkit-scrollbar-thumb {
  background-color: $apple;
  border-radius: 10px;
}

body {
    display: flex;
    flex-direction: column;
}

body.lock {
    overflow: hidden;
}

#root {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex: 1;
}

.app {
    display: flex;
    flex-direction: column;
    flex: 1;
    &.lock {
        overflow: hidden;
    }
}

.wrapper {
    display: flex;
    flex-direction: column;
    flex: 1;
}

.svg-icon {
    width: 25px;
    &-item {
        width: 15px;
    }
}

.svg-white {
    fill: $white;
}

.swiper-pagination-bullet {
    width: 20px !important;
    height: 20px !important;
    background: rgba(234, 28, 38, 0.7) !important;
    transform: scale(0.7);
    opacity: 1 !important;
    &-active {
        border: 4px solid $black;
        background: $white !important;
        transform: scale(1);
    }
}

.swiper-button-next, .swiper-button-prev {
    color: $themeColor !important;
    opacity: 1 !important;
    visibility: visible ;
    transform: scale(1) !important;
    transition: all 0.2s ease-in-out;
}

.swiper-button-next:after, .swiper-button-prev:after {
    font-size: 24px !important;
    font-weight: 700;
}

.swiper-button-disabled {
    opacity: 0 !important;
    visibility: hidden;
    transform: scale(0) !important;
    transition: all 0.2s ease-in-out;
}

.swiper-button-prev, .swiper-rtl .swiper-button-next {
    left: 0 !important;
}

.swiper-button-next, .swiper-rtl .swiper-button-prev {
    right: 0 !important;
}

.swiper-lazy-preloader {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    border: 0;
    transform: none;
    animation: none;
    border-radius: 0;
    margin: 0;
    background-color: white;
    span {
        width: 42px;
        height: 42px;
        position: absolute;
        left: 50%;
        top: 50%;
        margin-top: -21px;
        margin-left: -21px;
        z-index: 10;
        transform-origin: 50%;
        animation: rotate 1s infinite linear;
        border: 4px solid black;
        border-radius: 50%;
        border-top-color: transparent;
    }
}

@keyframes rotate {
    from {transform: rotate(0);}
    to {transform: rotate(360deg);}
}

.none-cart {
    text-align: center;
    font-size: 18px;
    line-height: 22px;
    margin: 30px 0;
    display: block;
}

.shop-product {
    margin-bottom: 30px;
}

.product-button {
    margin-top: 15px;
}

.shop_quantity {
    input {
        margin: 10px 0;
        font-size: 20px;
        color: $black;
        width: 80px;
        height: 40px;
        padding: 0 5px 0 10px;
        background: none;
        border: 1px solid $borderColor;
    }
}

.svg-item-icon {
    margin-right: 5px;
    width: 15px;
    fill: $bodyColor;
    transition: all 0.2s ease-in-out;
}

.svg-modal-social {
    width: 15px;
}

.item-to-wishlist {
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    &:hover {
       transition: all 0.2s ease-in-out;
       color: $themeColor;
       svg {
        fill: $themeColor;
        transition: all 0.2s ease-in-out;
       }
    }
}

.about-icon {
    width: 50px;
    fill: $themeColor;
}

.contacts-icon {
    fill: $bodyColor;
    width: 20px;
    transition: all 0.2s ease-in-out;
    &:hover {
       transition: all 0.2s ease-in-out;
       fill: $themeColor;
    }
}

.infinite-scroll-component {
    @media (min-width: 576px) {
       display: flex;
       flex-wrap: wrap;
    }
}

.shop-product {
     @media (min-width: 768px) {
        width: calc(50% - 10px);
        margin-right: 20px;
        &:nth-child(2n) {
            margin-right: 0;
        }
     }
     @media (min-width: 1200px) {
        width: calc(33.33333% - 20px);
        margin-right: 30px;
        &:nth-child(2n) {
            margin-right: 30px;
        }
        &:nth-child(3n) {
            margin-right: 0;
        }
     }
}

.main-text {
    color: $white;
    h2, h5 {
        color: inherit;
    }
}

.hero-area-bg {
    position: relative;
    &:before {
       content: '';
       position: absolute;
       top: 0;
       left: 0;
       width: 100%;
       height: 100%;
       background: rgba(0,0,0,.4);
    }
}

.offcanvas-close {
    position: relative;
    transition: all 0.2s ease-in-out;
    &:hover {
       transition: all 0.2s ease-in-out;
       span {
        background: $themeColor;
       }
    }
    span {
        height: 2px;
        width: 17px;
        background: $black;
        display: inline-block;
        position: absolute;
        top: 50%;
        left: 50%;
        border-radius: 2px;
        &:first-child {
            transform: translate(-50%, -50%) rotate(45deg);
        }
        &:last-child {
            transform: translate(-50%, -50%) rotate(-45deg);
        }
    }
}

.none-image {
    position: relative;
    &-item {
    }
}

.none-detail {
    position: relative;
    top: -150px;
    max-height: 500px;
    @media (max-width: 768px) {
       top: 0;
       max-height: none;
    }
}

.filter-modal {
    overflow-y: auto;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-100%);
    transition: all 0.2s ease-in-out;
    &.open {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
        transition: all 0.2s ease-in-out;
    }
}

.modal-body-filter {
    background: $white;
    margin: 50px;
    overflow-y: auto;
    padding: 10px 10px 40px 40px;
}

.modal-close-block {
    display: flex;
    justify-content: flex-end;
}

#rcc-confirm-button {
    transition: all 0.2s ease-in-out;
    &:hover {
       transition: all 0.2s ease-in-out;
       opacity: 0.6;
    }
}

.checkout_btn {
    &.disabled {
        cursor: not-allowed;
        opacity: 0.5;
    }
}

.swiper-autoheight .swiper-wrapper {
    align-items: stretch;
}

.subCategories__li {
    text-align: center !important;
    margin-right: 15px !important;
    span {
        max-width: 130px !important;
    }
}