
/*****************************
Pages - About Us
*****************************/

/* About Us Top Area */
.about-us-top-content{
    margin-bottom: -12px;
    h4{
        margin-top: 27px;
        margin-bottom: 15px;
    }
    p{
        padding: 0 0;
        line-height: 26px;
        margin-bottom: 0;
        @include breakpoint(medium){padding: 0 2%;}
        @include breakpoint(large){padding: 0 8%;}
        @include breakpoint(xlarge){padding: 0 12%;}
    }
}

/* About Us Center Area */
.about-us-center-area,
.about-us-bottom-area{
    margin-bottom: -37px;
}
.about-us-center-content{
    h4{
        margin-bottom: 30px;
    }
}

.about-promo-single-item{
    text-align: center;
    margin-bottom: 30px;

    img {
        width: 90px;
        height: 90px;
    }

    h6 {
        font-weight: 700;
        font-size: 18px;
        margin-top: 20px;
        margin-bottom: 15px;
    }
    
}

.about-feature-single-item {
    text-align: center;
    margin-bottom: 30px;

    h6 {
        font-weight: 700;
        font-size: 18px;
        margin-top: 20px;
        margin-bottom: 15px;
    }
    
}

.history {
    margin-bottom: 100px;
    margin-top: 100px;
    @media (max-width: 1919px) {
        margin-top: 60px;
    }
    @media (max-width: 991px) {
        margin-top: 40px;
    }
    @media (max-width: 767px) {
        margin-bottom: 30px;
    }
    &__container {
    }

    &__title {
    }
    &__swiper {
    }
    &__slide {
        width: 100%;
        img {
           max-width: 100%;
           width: 100%;
           object-fit: cover;
        }
    }
    &__content {
        margin-top: 110px;
        @media (max-width: 1919px) {
            margin-top: 70px;
        }
        @media (max-width: 991px) {
            margin-top: 40px;
        }
        @media (max-width: 767px) {
            margin-top: 80px;
        }
        @media (max-width: 575px) {
            margin-top: 30px;
        }
    }
    &__item {
        display: flex;
        align-items: flex-start;
        margin-bottom: 150px;
        position: relative;
        @media (max-width: 767px) {
            flex-direction: column;
            box-shadow: 2px 2px 19px rgba(0, 0, 0, 0.08);
            margin-bottom: 50px;
            padding: 30px;
        }
        &:nth-child(2n) {
            .history__left {
                order: 2;
                @media (max-width: 767px) {
                    order: 0;
                }
            }
            .history__right {
                padding-left: 0;
                order: 0;
                @media (max-width: 767px) {
                    order: 2;
                }
            }
        }

        &:last-child {
            margin-bottom: 0;
        }
        &-descr {
            font-size: 24px;
            line-height: 29px;
            color: #000000;
            display: flex;
            flex-direction: column;
            padding: 0 120px 0 0;
            @media (max-width: 1919px) {
                font-size: 18px;
                line-height: 22px;
                padding: 0 50px 0 0;
            }
            @media (max-width: 1250px) {
                padding: 0;
                max-width: 366px;
            }
            @media (max-width: 991px) {
                max-width: 304px;
            }
            @media (max-width: 767px) {
                max-width: none;
            }
            @media (max-width: 575px) {
                font-size: 14px;
                line-height: 18px;
            }
            span {
                a {
                    color: $themeColor;
                    transition: all 0.2s ease-in-out;
                    &:hover {
                        transition: all 0.2s ease-in-out;
                        color: $themeColor;
                        text-decoration: none;
                        opacity: 0.6;
                    }
                }
            }
        }
    }
    &__left,
    &__right {
        width: 50%;
        @media (max-width: 767px) {
            width: 100%;
        }
    }
    &__right {
        padding: 0 40px;
        @media (max-width: 767px) {
            padding: 0;
            margin-top: 30px;
        }
    }
}


