/*Header Center Area*/
.header-center{
    padding: 30px 0;
    border-top: 1px solid $borderColor;
    border-bottom: 1px solid $borderColor;
}

.header-action-icon{
    display: flex;
    justify-content: flex-end;
    & > li{
        margin-right: 40px;
        cursor: pointer;
        &:last-child{margin-right: 0;}
    }

    & > li > span{
        position: relative;
        font-size: 27px;
        color: #333;
        &:hover{
            color: $themeColor;
        }
    }

    & > li > span:hover .header-action-icon-item-count{
        background: $themeColor;
    }
}


.header-action-icon-item-count{
    position: absolute;
    top: 0;
    right: -8px;
    font-size: 10px;
    display: inline-block;
    background: #333;
    color: $white;
    text-align: center;
    line-height: 20px;
    border-radius: 50%;
    width: 18px;
    height: 18px;
    transition: $baseTransition;
}


.header {
    &__li {
        position: relative !important;
        transition: all 0.2s ease-in-out;
        &.scroll {
            .header__submenu {
                top: 100px;
            }
            transition: all 0.2s ease-in-out;
            &:hover {
               transition: all 0.2s ease-in-out;
               .header__submenu {
                top: 60px;
               }
            }
        }
        img.arrow-down {
            transition: all 0.2s ease-in-out;
        }
        &:hover {
           transition: all 0.2s ease-in-out;
           .header__submenu {
            opacity: 1;
            visibility: visible;
            top: 60px;
           }
           img.arrow-down {
            transition: all 0.2s ease-in-out;
            transform: rotate(180deg);
           }
        }
    }
    &__submenu {
        position: absolute;
        left: 0;
        opacity: 0;
        visibility: hidden;
        top: 100px;
        z-index: 50;
        transition: all 0.2s ease-in-out;
        box-shadow: 0px 10px 8px 0px rgba(0, 0, 0, 0.15);
        @media (max-width: 991px) {
           position: relative;
           box-shadow: none;
           top: 0;
           width: 100%;
           opacity: 1;
           visibility: visible;
           margin-top: 10px;
        }
        //&:before {
        //    content: "";
        //    position: absolute;
        //    top: 100%;
        //    left: 0;
        //    width: 100%;
        //    height: 100vh;
        //    overflow: hidden;
        //    background: rgba(10, 10, 10, 0.74);
        //}
    }

    &__subnav {
        background:#fff;
        padding: 35px;
        display: flex;
        flex-direction: column;
        @media (max-width: 991px) {
           margin-top: 0;
           padding:  0 15px;
        }
        &-elem {
            margin-bottom: 20px;
            @media (max-width: 991px) {
               margin-bottom: 10px;
            }
            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &__sublink {
        font-size: 16px;
        line-height: 20px;
        color: $themeColor;
        font-weight: 700;
        transition: all 0.2s ease-in-out;
        @media (max-width: 991px) {
           font-size: 14px;
           line-height: 18px;
           font-weight: 400;
        }
        img {
            height: 20px;
            @media (max-width: 991px) {
               height: 15px;
            }
        }
        &:hover {
            transition: all 0.2s ease-in-out;
            text-decoration: none;
            opacity: 0.4;
            color: $themeColor;
        }
    }

    &__sublist {
        margin-top: 40px;
    }

    &__subli {
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: 0;
        }
        &-link {
            font-size: 26px;
            line-height: 32px;
            color: $black;
            border-bottom: 1px solid transparent;
            transition: all 0.2s ease-in-out;
            &:hover {
                transition: all 0.2s ease-in-out;
                color: $themeColor;
                text-decoration: none;
                border-bottom: 1px solid $themeColor;
            }
        }
    }
}