/*****************************
Components - Table
*****************************/

table {
    width: 100%;
}

.table-responsive{
    position: relative;
    z-index: 1;
}

.view{color: $themeColor;}

.table_desc {
    border: 1px solid $borderColor;
}

.table_page table thead tr:last-child th, 
.table_desc table tbody tr td:last-child {
    border-right: 0;
}

.table-responsive table thead {
    background: #f2f2f2;
}

 .table_page table thead tr th {
    color: $headingColor;
    border-bottom: 3px solid $themeColor;
    border-right: 1px solid $borderColor;
    font-size: 16px;
    font-weight: 600;
    text-transform: capitalize;
    padding: 10px;
    text-align: center;
    @media (max-width: 992px) {
       font-size: 12px;
    }
}

 .table_page table tbody tr td {
    border-bottom: 1px solid $borderColor;
    border-right: 1px solid $borderColor;
    text-align: center;
    padding: 10px;
    @media (max-width: 992px) {
       padding: 5px;
    }
}

.table-responsive table tbody tr td {
    border-right: 1px solid $borderColor;
    font-weight: 500;
    text-transform: capitalize;
    font-size: 14px;
    text-align: center;
    min-width: 130px;
    @media (max-width: 992px) {
       min-width: 20px;
    }
}
.table_page table tbody tr td{
    &.product_remove {
        min-width: 100px;
        font-size: 20px;
        @media (max-width: 992px) {
           min-width: auto;
           max-width: 20px;
           padding: 0;
           font-size: 14px;
        }
        span {
            cursor: pointer;
            transition: all 0.2s ease-in-out;
            &:hover {
               transition: all 0.2s ease-in-out;
               color: #ea1c26;
            }
        }
    }
    &.product_thumb {
        max-width: 180px;
        overflow: hidden;
        img{
            width: 92px;
            height: 92px;
            object-fit: cover;
        }
    }
    &.product_name {
        min-width: 180px;
        @media (max-width: 992px) {
           text-align: left;
           min-width: 135px;
        }
        span {
            @media (max-width: 992px) {
               text-decoration: underline;
            }
        }
    }
    &.product-price {
        min-width: 130px;
        color: #333;
        font-size: 16px;
        font-weight: 500;
        @media (max-width: 992px) {
           min-width: 60px;
           font-size: 14px;
        }
    }

    &.product_addwishlist {
        svg {
            transition: all 0.2s ease-in-out;
            cursor: pointer;
            &:hover {
               transition: all 0.2s ease-in-out;
               fill: #ea1c26;
            }
        }
    }

    &.product_quantity {
        min-width: 120px;
        user-select: none;
        @media (max-width: 992px) {
           min-width: 70px;
        }
        label{
            font-weight: 600;
            margin-right: 5px;
        }
        input {
            width: 80px;
            height: 40px;
            padding: 0 5px 0 10px;
            background: none;
            border: 1px solid $borderColor;
            @media (max-width: 992px) {
               width: 40px;
            }
        }
        .product-item {
            width: 30px;
            height: 30px;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            font-size: 20px;
            background: $themeColor;
            color: $white;
            cursor: pointer;
            transition: all 0.1s ease-in-out;
            &:hover {
               transition: all 0.1s ease-in-out;
               opacity: 0.6;
            }
        }
        .product-count {
            margin: 0 20px;
            font-size: 20px;
            color: $black;
            @media (max-width: 992px) {
               margin: 0 5px;
               font-size: 16px;
            }
        }
    }

    &.product_add {
        .product-action-icon-link-relative {
            li {
                &:hover {
                    svg {
                     fill: $white;
                    }
                 }
            }
        }
        li {
            cursor: pointer;
            font-size: 14px;
            line-height: 16px;
            list-style-type: none;
            max-width: 100px;
            margin: 0 auto;
            margin-top: 15px;
            text-transform: none;
            color: $black;
            &:hover {
                color: $themeColor;
                text-decoration: underline;
                svg {
                    fill: $themeColor;
                }
            }
            svg {
                width: 15px;
            }
        }
    }

    &.product_addcart {
        min-width: 150px;
        span {
            background: $themeColor;
            font-size: 12px;
            font-weight: 700;
            height: 38px;
            line-height: 18px;
            padding: 10px 20px;
            color: $white;
            text-transform: uppercase;
            border-radius: 3px;
            cursor: pointer;
            transition: all 0.2s ease-in-out;
            &:hover{
                background: #333;
                transition: all 0.2s ease-in-out;
            }
        }
    }
    
}
