/*****************************
Components - Sidebar Widget
*****************************/
.siderbar-section{
    margin-top: 60px;
    position: sticky;
    top: 75px;
    @include breakpoint(medium){
        margin-top: 80px;
    }
    @include breakpoint(large){
        margin-top: 0;
    }
}
.siderbar-section-mob {
    margin-top: 20px;
}
.sidebar-single-widget{
    margin-top: 20px;
    margin-bottom: 10px;
    padding-bottom: 10px;
    border-bottom: 2px solid $gray-100;
    &:first-child{
        margin-top: 0px;
    }
    &:last-child{
        border-bottom: none;
        margin-bottom: 0;
        padding-bottom: 0;
    }
}
.sidebar-title{
    text-transform: capitalize;
    margin-bottom: 30px;
    font-weight: 600;
}

.sidebar-banner {
    margin: 0 auto;
    display: block;
    @media (max-width: 992px) {
        padding-right: 30px;
     }
}

.sidebar-banner-link {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    min-height: 150px;
    border: 1px solid $themeColor;
    font-size: 20px;
    line-height: 24px;
}

@import "sidebar-widgets/price-range";
@import "sidebar-widgets/select-type";
@import "sidebar-widgets/tag-link";
@import "sidebar-widgets/recent-post";

