/*Header Top Area*/
.header-top{
    font-size: 13px;
}

.mobile-logo-img {
    width: 50px;
}

.header-logo {
    transition: all 0.2s ease-in-out;
    &:hover {
       transition: all 0.2s ease-in-out;
    }
    &-text {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-left: 5px;
        color: $black;
        &-1 {
            font-size: 22px;
            line-height: 22px;
            margin-bottom: 5px;
            @media (max-width: 575px) {
               font-size: 18px;
               line-height: 18px;
            }
        }
        &-2 {
            font-size: 12px;
            line-height: 14px;
            @media (max-width: 575px) {
               font-size: 10px;
               line-height: 12px;
            }
        }
    }
    &-link {
        display: flex;
        align-items: center;
    }
    img {
        width: 70px;
    }
}

.mobile-header--left {
    margin-right: 50px;
}

.header__sticky-logo {
    padding-right: 0;
    transform: scale(0);
    opacity: 0;
    visibility: hidden;
    position: absolute !important;
    transition: all 0.2s ease-in-out;
    &.active {
        padding-right: 50px;
        transform: scale(1);
        opacity: 1;
        visibility: visible;
        position: relative !important;
        transition: all 0.2s ease-in-out;
    }
    img {
        width: 50px;
    }
    &-link {
        background: none !important;
        padding: 0 !important;
    }
}

.header-user-menu{
    display: flex;
    align-items: center;
    justify-content: flex-end;

    & > li{
        margin-right: 40px;
        &:last-child{margin-right: 0;}
        position: relative;
        &::after{
            position: absolute;
            content: "|";
            top: 50%;
            transform: translateY(-50%);
            right: calc(0% - 20px);
        }
        &:last-child::after{
            display: none;
        }
    }

    & > li > a{
        display: inline-block;
        line-height: 46px;
    }
}


.has-user-dropdown{
    position: relative;
    padding-right: 15px;
    &::before{
        content: "\f107";
        font-family: "FontAwesome";
        position: absolute;
        top: calc(50%);
        right: 0;
        transform: translateY(-50%);
        font-weight: 400;
    }
}

.user-sub-menu{
    position: absolute;
    top: calc(100%);
    transform: translateY(30px);
    left: calc(0% - 20px);
    right: auto;
    padding: 10px 15px;
    width: 160px;
    background: $white;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    border-radius: 3px;
    transition: $baseTransition;
    visibility: hidden;
    opacity: 0;
    z-index: 99;

    & > li{
        display: block;
        border-bottom: 1px solid $borderColor;
        padding: 5px 0;
        &:last-child{border-bottom: none;}
    }

    & > li > a{
        position: relative;
        display: flex;
        align-items: center;
        &:hover{color:$themeColor;}
    }
}


.user-sub-menu-in-icon{padding-right: 5px;}

.has-user-dropdown:hover .user-sub-menu{
    visibility: visible;
    opacity: 1;
    transform: translateY(0px);
}